export default {
  data() {
    return {
      // 左侧权限数据
      menulist: [],
      // 权限图标
      iconlist: [
        'icon-users',
        'icon-tijikongjian',
        'icon-shangpin',
        'icon-danju',
        'icon-baobiao'
      ],
      // 控制左边栏是否显示,false:显示  true:不显示
      leftshow: false
    }
  },
  created() {
    this.getMenuList()
  },
  methods: {
    // 获得左侧列表数据
    async getMenuList() {
      const { data: res } = await this.$http.get('/menus')
      // console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('左侧权限数据获取失败!')
      }
      this.menulist = res.data
    },
    // 退出系统
    logout() {
      this.$confirm('确定要退出系统?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          window.sessionStorage.removeItem('token')
          this.$router.push('/login')
        })
        .catch(() => {})
    }
  }
}



