export default {
  data() {
    var checkMobile = (rule, value, callback) => {
      if (!value.trim()) {
        return callback(new Error('手机号码不能为空！'))
      }
      if (!/^1[34578]\d{9}$/.test(value)) {
        return callback(new Error('手机号码有误，请重填！'))
      } else {
        callback()
      }
    }
    return {
      // 用户列表数据
      userList: [],
      // 查询用户的参数对象
      queryInfo: {
        // 搜索条件
        query: '',
        // 展示第n页数据
        pagenum: 1,
        // 每页显示条数
        pagesize: 2,
        // 记录总条数
        total: 0
      },

      // 添加用户的弹层是否显示，默认不显示
      addDialogVisible: false,
      // 添加用户的表单数据对象
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      addFormRules: {
        username: [
          { required: true, message: '请输入用户名称', trigger: 'blur' }
        ],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
        email: [{ required: true, message: '请输入邮箱', trigger: 'blur' }],
        mobile: [{ validator: checkMobile, trigger: 'blur' }]
      },
      // 修改用户的弹层是否显示，默认不显示
      editDialogVisible: false,
      // 修改用户的表单数据对象
      editForm: {
        username: '',
        email: '',
        mobile: ''
      },
      editFormRules: {
        email: [{ required: true, message: '请输入邮箱', trigger: 'blur' }],
        mobile: [{ validator: checkMobile, trigger: 'blur' }]
      },

      /** 给用户分配角色1 */
      // 给用户分配角色 弹层 默认不显示
      roleDialogVisible: false,
      // 修改用户的表单数据对象
      roleForm: {
        username: '',
        role_name: ''
      },
      roleFormRules: {},
      // 全部角色信息
      rolesList: [],
      // 选中的角色
      newUserRoleId: ''
      /** 给用户分配角色2 */
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    /** 给用户分配角色1 */
    // 给用户分配角色-展示弹层
    async showRoleDialogVisible(userinfo) {
      this.roleDialogVisible = true
      // 给用户分配角色 用户表单数据部分
      this.roleForm = userinfo
      // 获取用于分配的角色信息
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message({
          message: '获得角色列表失败！',
          type: 'error',
          duration: 1000
        })
      }
      this.rolesList = res.data
    },
    // 收集数据存储
    async roleUser() {
      if (!this.newUserRoleId) {
        return this.$message({
          message: '请选取要分配的角色',
          type: 'error',
          duration: 1000
        })
      }
      const { data: res } = await this.$http.put(
        'users/' + this.roleForm.id + '/role',
        { rid: this.newUserRoleId }
      )
      if (res.meta.status !== 200) {
        return this.$message({
          message: '给用户分配角色失败',
          type: 'error',
          duration: 1000
        })
      }
      // 关闭弹框
      this.roleDialogClose()
      // 页面刷新
      this.getUserList()
      // 提示
      this.$message({
        message: '给用户分配角色成功',
        type: 'success',
        duration: 1000
      })
    },
    // 修改用户弹层关闭前回调(自然关闭，点击页面其他地方)
    roleDialogCloseBefore(done) {
      // 清除选中的角色
      // this.$refs.roleFormRef.resetFields()
      this.newUserRoleId = ''
      // 关闭弹层
      done()
    },
    // 点击“取消”按钮的回调
    roleDialogClose() {
      // 清除选中的角色
      this.newUserRoleId = ''
      // 关闭弹层
      this.roleDialogVisible = false
    },
    /** 给用户分配角色2 */

    // 删除用户
    async delUser(id) {
      const cfm = await this.$confirm('确定要删除该用户么?', '删除', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)

      if (cfm === 'confirm') {
        const { data: res } = await this.$http.delete('users/' + id)

        if (res.meta.status !== 200) {
          return this.$message({
            message: '用户删除失败！',
            type: 'error',
            duration: 1000
          })
        }

        this.$message({
          message: '用户删除成功！',
          type: 'success',
          duration: 1000
        })

        // 如果当前页码只有一条记录，并且被删除了，那么页码就要跳转到前一页显示
        if (this.userList.length === 1 && this.queryInfo.pagenum > 1) {
          this.queryInfo.pagenum--
        }
        // 刷新页面
        this.getUserList()
      }
    },
    // 添加用户
    addUser() {
      // 校验用户信息
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return null
        }

        // 实现数据添加
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status !== 201) {
          return this.$message({
            message: '添加用户失败！',
            type: 'error',
            duration: 1000
          })
        }
        // 关闭弹层
        this.addDialogVisible = false
        // 页面刷新
        this.getUserList()
        // 清除form表单信息
        this.$refs.addFormRef.resetFields()
        this.$message({
          message: '添加用户成功！',
          type: 'success',
          duration: 1000
        })
      })
    },

    // 修改用户状态
    async changeState(id, state) {
      const { data: res } = await this.$http.put(`users/${id}/state/${state}`)
      if (res.meta.status !== 200) {
        return this.$message.error('修改用户状态失败！')
      }
      this.$message({
        message: '修改状态成功',
        type: 'success',
        duration: 1500
      })
    },
    // 获得用户列表信息
    async getUserList() {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('获取用户信息失败！')
      }
      this.userList = res.data.users
      // 更新总记录数目
      this.queryInfo.total = res.data.total
    },

    // 分页相关
    // 设定每页显示条数
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 设定显示第n页数据
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    // 添加用户弹层关闭前回调
    addDialogClose(done) {
      // 清除校验结果
      this.$refs.addFormRef.resetFields()
      // 关闭弹框
      done()
    },
    // 修改用户信息服务器处理
    editUser() {
      // 校验用户信息
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) {
          return null
        }

        // 实现数据修改
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          this.editForm
        )
        if (res.meta.status !== 200) {
          return this.$message({
            message: '修改用户失败！',
            type: 'error',
            duration: 1000
          })
        }
        // 关闭弹层
        this.editDialogVisible = false
        // 页面刷新
        this.getUserList()
        // 清除form表单信息
        this.$refs.editFormRef.resetFields()
        this.$message({
          message: '修改用户成功！',
          type: 'success',
          duration: 1000
        })
      })
    },
    // 展示 修改 用户的弹层
    async showEditDialog(id) {
      // 根据id获得当前被修改用户信息
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) {
        return this.$message({
          message: '获取用户信息失败！',
          type: 'error',
          duration: 1000
        })
      }
      // 把用户信息赋予给form表单数据对象
      this.editForm = res.data
      this.editDialogVisible = true
    },
    // 修改用户弹层关闭前回调(自然关闭，点击页面其他地方)
    editDialogCloseBefore(done) {
      // 清除校验结果
      this.$refs.editFormRef.resetFields()
      // 关闭弹层
      done()
    },
    // 点击“取消”按钮的回调
    editDialogClose() {
      // 清除校验结果
      this.$refs.editFormRef.resetFields()
      // 关闭弹层
      this.editDialogVisible = false
    }
  }
}



